import {
	get_audio_list,
	get_article_list,
	get_audio_by_id,
	get_reading_together,
	get_article_by_audio_id,
	getWeinConfig,
	insert_audio,
	insert_article,
	get_all_category,
	increase_read_amount,
	get_article_by_id
} from '@/api/huting/index.js'

export default {
	state: {
		sortType: 1, // 
		content: '', // 
		audioInfo: {}, // 
		audioOrActicle: null, // 
		type: null, //  
		voicePath: '', //  
		localId: null, // id
		flag: false, // 
		audioOrauthor: null, // id
		allTime: 0 // 
	},
	getter: {
		getFlag(state) {
			return state.flag
		}
	},
	mutations: {
		// 
		setSortType(state, value) {
			state.sortType = value
		},

		//
		setContent(state, value) {
			state.content = value
		},

		// audioInfo
		setAudioInfo(state, value) {
			state.audioInfo = value
		},

		// 
		setAudioOrActicle(state, value) {
			state.audioOrActicle = value
		},

		//  
		setType(state, value) {
			state.type = value
		},

		//    
		setVoicePath(state, value) {
			state.voicePath = value
		},

		//id
		setLocalId(state, value) {
			state.localId = value
		},

		//  flag
		setFlag(state, value) {
			state.flag = value
		},

		// id
		setAudioOrauthor(state, value) {
			state.audioOrauthor = value
		},

		setallTime(state, value) {
			state.allTime = value
		},
	},
	actions: {
		// -
		get_audio_list({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				get_audio_list(obj).then(res => {
					// console.log(res.data.data, "-");
					resolve(res.data.data)
				})
			})
		},

		//-
		get_article_list({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				get_article_list(obj).then(res => {
					// console.log(res.data.data, "-");
					resolve(res.data.data)
				})
			})
		},

		// id
		get_audio_by_id({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				get_audio_by_id(obj).then(res => {
					// console.log(res.data, "id");
					resolve(res.data.data)
				})
			})
		},

		// -
		get_reading_together({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				get_reading_together(obj).then(res => {
					console.log(res.data, "-");
					resolve(res.data.data)
				})
			})
		},

		// -
		get_article_by_audio_id({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				get_article_by_audio_id(obj).then(res => {
					// console.log(res.data, "-");
					resolve(res.data)
				})
			})
		},

		// js sdk config json
		getWeinConfig({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				getWeinConfig(obj).then(res => {
					// console.log(res, "js sdk config json");
					resolve(res)
				})
			})
		},

		// 
		insert_audio({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				insert_audio(obj).then(res => {
					// console.log(res, "");
					resolve(res)
				})
			})
		},

		// 
		insert_article({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				insert_article(obj).then(res => {
					// console.log(res, "");
					resolve(res)
				})
			})
		},

		// 
		get_all_category({
			state,
			commit,
			rootState
		}) {
			return new Promise((resolve, reject) => {
				get_all_category().then(res => {
					// console.log(res.data, "");
					resolve(res.data.data)
				})
			})
		},

		// 
		increase_read_amount({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				increase_read_amount(obj).then(res => {
					// console.log(res, "");
					resolve(res)
				})
			})
		},
		
		//  id 
		get_article_by_id({
			state,
			commit,
			rootState
		}, obj) {
			return new Promise((resolve, reject) => {
				get_article_by_id(obj).then(res => {
					// console.log(res, " id ");
					resolve(res.data.data)
				})
			})
		},

	}
}
