import request from '../index.js'

let http = request.http

let http1 = request.http1

// -
// sortType	false	number	null	 1- 2-
export const get_audio_list = ({
	sortType,
	pageNum,
	pageSize,
	content
}) => {
	return http.get('/listen/get_audio_list', {
		params: {
			sortType,
			pageNum,
			pageSize,
			content
		}
	})
}

