export const insert_article = ({
	userId,
	// articleAuthorName,
	cover,
	articleTitle,
	articleIntroduction,
	articleContent,
	articleReadAmount,
	circleId,
	articleType,
	labelIdListStr,
	articleWords
}) => {
	return http.post('/article/insert_article', {
		// userId,
		// articleAuthorName,
		cover,
		articleTitle,
		articleIntroduction: '',
		articleContent,
		articleReadAmount: 0,
		circleId: 1,
		articleType: 2,
		labelIdListStr,
		articleWords
	})
}


