export const arrChaji = (arr2, arr1) => {
	let arr3 = []
	arr1.map((item1) => {
		arr2.map((item2) => {
			if (item1.categoryId == item2.categoryId) {
				//
				item1.isRepeat = true;
			}
		})
	});
	arr1.map((item) => {
		//
		if (!item.isRepeat) {
			arr3.push(item);
		}
	});
	// console.log(arr3, 'aaa');
	return arr3
}
