export default {
	state: {
		bannerList: [],
		audio: {
			src: '',
			title: '',
			singer: '',
			coverImgUrl: ''
		},
		playinfo: {
			current: 0, //
			duration: 0, //
			duration_value: 0, //
			current_value: 0, //
			src: ""
		},
		paused: true, // 
		n_pause: false, //
		type: 'zhangjie' ,// 
		gloalImg:'' // 
	},
	mutations: {
		setaudio(state, data) {
			state.audio = data;
		},
		setnextaudio(state, data) {
			state.nextaudio = data
		},
		setplay(state, data) {
			if (data.current) {
				state.playinfo.current = data.current;
			}
			if (data.duration) {
				state.playinfo.duration = data.duration;
			}
			if (data.duration_value) {
				state.playinfo.duration_value = data.duration_value;
			}
			if (data.current_value) {
				state.playinfo.current_value = data.current_value;
			}
			if (data.current_value) {
				state.playinfo.current_value = data.current_value;
			}
			if (data.src) {
				state.playinfo.src = data.src;
			}
		},
		setpause(state, data) {
			state.paused = data
		},
		set_n_pause(state, data) {
			state.n_pause = data
		},
		setAudioType(state, data) {
			state.type = data
		},
		setGloalImg(state, data) {
			state.gloalImg = data
		},
	},
	actions: {},
}
