import {
	tabBarList
} from './data.js'


export default {
	state: {
		tabBarList,
		key: '', // key
		code: '', // 
		phone: '', // 
		token: '' // token
	},
	mutations: {
		setPhoneKey(state, value) {
			state.key = value
		},
		setPhoneCode(state, value) {
			state.code = value
		},
		setPhone(state, value) {
			state.phone = value
		},
		setToken(state, value) {
			state.token = value
		},
	},
	actions: {},
}
